@import "_normalize";
@import "_variables";
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap&subset=latin-ext");
@import url("https://fonts.googleapis.com/css?family=Arizonia|Damion&display=swap");
// font-family: 'Damion', cursive;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #222;
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  background-color: #fefefe;
}

.site-header {
  background-color: #444;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;

  &__wrapper {
    margin: 0 auto;
    width: 90%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.05);
  }

  @media (max-width: 640px) {
    display: none;
  }

  &__image {
    height: 2.6rem;

    @media (max-width: 765px) {
      height: 2rem;
    }
  }

  &__name {
    font-family: "Damion", cursive;
    font-size: 2.5rem;
    color: #a5ce39;
    margin-top: 4px;
    margin-left: 7px;

    @media (max-width: 765px) {
      font-size: 2rem;
    }
  }
}

.navigation {
  &__list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0 20px 0;
  }

  &__item {
    &:not(:last-child) {
      border-right: 2px solid white;
      padding: 0 30px 0 30px;
    }

    &:last-child {
      padding: 0 20px 0 20px;
    }
  }

  &__link {
    color: white;
    text-decoration: none;
    cursor: pointer;
    font-size: 18px;
    transition: 0.3s;

    @media (max-width: 765px) {
      font-size: 14px;
    }

    &:hover {
      cursor: pointer;
      color: rgb(235, 125, 52);
    }
  }
}

.hamburger {
  display: none;
  position: fixed;
  top: 20px;
  right: 30px;
  font-size: 25px;
  color: #444;
}

main {
  margin: 0 auto;
  width: 90%;
  max-width: 1024px;
}

.header {
  margin-top: 7rem;
  margin-bottom: 1rem;

  &__text {
    color: #40476d;
    font-size: 32px;
    font-family: "Montserrat", sans-serif;

    &--red {
      color: tomato;
    }
  }

  &__button {
    border-style: none;
    background-color: #444;
    color: white;
    padding: 10px;
    border-radius: 5px;
    transition: 0.3s;

    &:hover {
      background-color: #555;
    }
  }
}

.about__image {
  width: 40%;
  float: left;
  margin-right: 40px;
  margin-bottom: 20px;
  box-shadow: 0 1rem 4rem rgba(black, 0.4);

  @media (max-width: 640px) {
    float: none;
    width: 70vw;
    box-shadow: 0 0.5rem 3rem rgba(black, 0.4);
  }
}

.clear {
  clear: both;
}

.diary {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;

  @media (max-width: 640px) {
    grid-template-columns: 1fr;
  }

  &__header {
    grid-column: 1 / span 2;
    color: rgb(235, 125, 52);
    margin-top: 1.5rem;

    @media (max-width: 640px) {
      grid-column: 1;
    }
  }

  &__second-header {
    color: rgb(83, 163, 43);
    margin-top: 1rem;

    &::before {
      content: "";
      display: inline-block;
      background-color: rgb(83, 163, 43);
      width: 14px;
      height: 14px;
      border-radius: 50%;
      margin-right: 8px;
    }
  }

  &__link {
    color: #ad5d4e;
    text-decoration: none;
    cursor: pointer;
    font-size: 14px;
    margin-top: 14px;

    &::after {
      content: "__________";
      display: block;
      color: #ad5d4e;
    }
  }

  &__image {
    width: 100%;
    margin: 2rem 0 2rem 0;
    box-shadow: 0 0.5rem 2rem grey;
  }

  &__form {
    grid-column: 1 / span 2;

    @media (max-width: 640px) {
      grid-column: 1;
    }
  }
}

.form {
  margin: 1rem 0;
  background-color: #ddd;
  padding: 2rem 1.5rem;
  border-radius: 10px;
  display: flex;
  align-items: baseline;
  @media (max-width: 640px) {
    flex-direction: column;
    padding: 1.5rem 1.5rem;
  }
  &__label {
    color: rgb(83, 163, 43);
    font-size: 14px;
    padding-right: 20px;
  }
  &__input {
    width: 30vw;
    padding: 5px 10px;
    font-size: 12px;
    font-style: italic;
    margin-right: 20px;
    border-style: none;
    border-radius: 5px;
    border: 1px solid rgb(83, 163, 43);
    @media (max-width: 640px) {
      margin: 10px 0;
    }
  }
  &__button {
    border-style: none;
    border: 1px solid rgb(83, 163, 43);
    background-color: #ddd;
    color: rgb(83, 163, 43);
    padding: 5px 10px;
    border-radius: 5px;
    transition: 0.5s;
    font-size: 13px;
    box-shadow: 0 1px 5px rgba(black, 0.4);

    &:hover {
      color: #fff;
      background-color: rgb(83, 163, 43);
      box-shadow: 0 2px 7px rgba(black, 0.5);
      transform: translateY(-2px) scale(1.1);
    }
  }
}

.about__paragraph,
.diary__paragraph,
.footer__paragraph {
  margin: 1.5rem 0 1rem 0;
  color: darkslategrey;
  line-height: 120%;
}

.about__paragraph--mod-1 {
  padding: 0 20px 0 20px;
  font-weight: 700;
  font-style: italic;
}

.about__header,
.glossary__header {
  color: rgb(235, 125, 52);
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.glossary {
  margin-bottom: 1rem;

  &__title,
  &__definition {
    font-size: 16px;
    color: darkslategrey;
    line-height: 150%;
  }

  &__title {
    font-weight: bold;
  }

  &__definition {
    font-style: italic;
    padding-bottom: 0.5rem;
  }
}

.footer {
  background-color: #444;
  margin-top: 2rem;
  padding: 1rem 0 1rem 0;

  &__wrapper {
    margin: 0 auto;
    width: 90%;
    max-width: 1024px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__leaf {
    height: 5rem;
    margin-right: 2rem;
    transition: transform 1s ease-in-out;

    &:hover {
      transform: rotate(360deg) scale(0.8);
    }
  }

  &__top {
    position: fixed;
    bottom: 15px;
    right: 25px;
  }

  &__link {
    color: #ad5d4e;
    text-decoration: none;
    cursor: pointer;
    font-size: 14px;
    margin-top: 14px;
  }

  &__paragraph {
    color: white;
    line-height: 160%;

    @media (max-width: 870px) {
      font-size: 14px;
    }
  }
}

@media (max-width: 640px) {
  .navigation__link {
    font-size: 14px;
  }

  .hamburger {
    display: block;
  }

  .offIcon {
    display: none;
  }

  .navigation__list {
    display: none;
    flex-direction: column;
    align-items: flex-start;
  }

  .changeDisplay {
    display: flex;
  }

  .navigation__item:not(:last-child) {
    border-right: 0 solid white;
    padding: 20px 30px 10px 30px;
  }

  .navigation__item:last-child {
    padding: 20px 30px 20px 30px;
  }

  .header {
    margin-top: 4rem;
  }

  .marginGoesDown {
    margin-top: 15rem;
  }
}
